








import Vue from 'vue';
import {ParameterGroup} from '@/configurator/store/core-data';
import {Nullable} from '@/common/utils/types';
import {UI_STATE_ACTIONS, UI_STATE_MUTATIONS} from '@/configurator/store/ui-state';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
import NextAction from '@/common/components/side-bar/NextAction.vue';
// @ts-ignore
import SvgArrowRight from '@/common/svgs/arrow-right.svg';

export default Vue.extend({
  components: {
    SvgIcon,
    NextAction,
  },

  computed: {
    icons(): object {
      return {SvgArrowRight};
    },
    nextGroup(): Nullable<ParameterGroup> {
      const groups = this.$store.state.coreData.groups;
      const selectedGroup = this.$store.state.uiState.selectedGroup;
      if (this.$store.state.uiState.showVariants && groups.length > 0) {
        return groups[0];
      }
      const nextIndex = groups.indexOf(selectedGroup) + 1;
      if (nextIndex <= 0 || nextIndex >= groups.length) {
        return null;
      }
      return groups[nextIndex];
    },
    showButton(): boolean {
      return !!this.nextGroup;
    },
  },

  methods: {
    setNextGroup() {
      if (this.nextGroup) {
        const groupId = this.nextGroup.key;
        this.$sdkConnector.configuratorApi.then((configurator) => configurator.setActiveGroupInView(groupId));
      }
      this.$store.dispatch(UI_STATE_ACTIONS.SELECT_GROUP, this.nextGroup);
      this.$store.commit(UI_STATE_MUTATIONS.SET_SHOW_VARIANTS, false);
    },
  },

});
